.btn-nav {
  position: relative;
  overflow: hidden;
  background: transparent;
  text-transform: uppercase;
  transition: all 0.33s;
  z-index: 1;
}

.btn-nav:before,
.btn-nav:after {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  max-height: 0;
  top: 0;
  left: 0;
  z-index: -1;
  transition: all 0.35s;
  @apply bg-unitermi-primary-redDark;
}

.btn-nav.current-menu-item:before,
.btn-nav.current-menu-item:after {
  @apply bg-unitermi-primary-redDark;
}

.btn-nav:before {
  opacity: 0.5;
}

.btn-nav:after {
  transition-delay: 0.2s;
}

.btn-nav:hover {
  color: #fff;
  background-color: #a51a1d;
}

.btn-nav.btn-nav.current-menu-item:hover {
  @apply text-unitermi-primary-redDark;
}

.btn-nav:hover:before,
.btn-nav:hover:after {
  max-height: 400%;
}

