*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

/* ======================================== */
/* ================scroolbar=================== */
/* ======================================== */
/* width */
::-webkit-scrollbar {
  width: 1vw;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background-color: #cccccc;
  border-radius: 10px;
  transition: background-color 0.8s;
}

::-webkit-scrollbar-corner {
  border-radius: 10px;
}

