.counter {
	display: flex;
	align-items: flex-end;
	justify-content: flex-start;
	cursor: default;
	&-card {
		display: flex;
		align-items: center;
		justify-content: space-between;
		flex-direction: column;
		align-content: center;
		transition: all 0.3s;
		&:hover {
			-webkit-box-shadow: 0px 0px 45px -4px rgba(0, 0, 0, 0.21);
			-moz-box-shadow: 0px 0px 45px -4px rgba(0, 0, 0, 0.21);
			box-shadow: 0px 0px 45px -4px rgba(0, 0, 0, 0.21);
		}
		&--rocket {
		}
	}
	&__brasil--titulo {
		font-size: 3rem;
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
		cursor: default;
		flex-direction: column;
	}
	&__main-title {
		font-size: 1.5rem;
	}
	&__subtitle {
		font-size: 1.4rem;
	}
	&__bandeira-img {
		max-width: 50%;
		margin: auto;
	}
	&__temp-img {
		max-width: 36%;
		margin: auto;
	}
	&__number {
		font-size: 4.2rem;
		line-height: 4.2rem;
		&--rocket {
			font-size: 7rem;
			line-height: 7rem;
		}
	}
}

@media (max-width: 1280px) {
	.counter {
		font-size: 3rem;
	}
}

@media (max-width: 700px) {
	.counter {
		display: flex;
		align-items: flex-end;
		justify-content: flex-start;
		&-card {
			display: flex;
			align-items: center;
			justify-content: center;
			flex-direction: column;
			align-content: center;
			transition: all 0.3s;
			margin: 0.5rem 3rem;
			&:hover {
				-webkit-box-shadow: 0px 0px 45px -4px rgba(0, 0, 0, 0.21);
				-moz-box-shadow: 0px 0px 45px -4px rgba(0, 0, 0, 0.21);
				box-shadow: 0px 0px 45px -4px rgba(0, 0, 0, 0.21);
			}
		}
		&-inner-wraper {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			& h2 {
				text-align: center;
			}
		}
		&__img {
			max-width: 39%;
			margin: auto;
		}
	}
}
@media (min-width: 1685px) {
	.counter {
		display: flex;
		align-items: flex-end;
		justify-content: flex-start;
		cursor: default;
		&-card {
			display: flex;
			align-items: center;
			justify-content: space-between;
			flex-direction: column;
			align-content: center;
			transition: all 0.3s;
			&:hover {
				-webkit-box-shadow: 0px 0px 45px -4px rgba(0, 0, 0, 0.21);
				-moz-box-shadow: 0px 0px 45px -4px rgba(0, 0, 0, 0.21);
				box-shadow: 0px 0px 45px -4px rgba(0, 0, 0, 0.21);
			}
			&--rocket {
				.counter {
					align-items: center;
					span {
						align-self: flex-end;
						margin-bottom: 1.6rem;
						margin-left: 1rem;
					}
				}
			}
		}
		&__brasil--titulo {
			font-size: 4.3rem;
			display: flex;
			align-items: flex-start;
			justify-content: flex-start;
			cursor: default;
			flex-direction: column;
		}
		&__main-title {
			font-size: 2.1rem;
		}
		&__subtitle {
			font-size: 1.9rem;
		}
		&__bandeira-img {
			max-width: 50%;
			margin: auto;
		}
		&__temp-img {
			max-width: 36%;
			margin: auto;
		}
		&__number {
			font-size: 6.7rem;
			line-height: 6.7rem;
			padding-left: 1rem;
			&--rocket {
				font-size: 7rem;
				line-height: 7rem;
			}
		}
	}
}
